html, body {
  margin: 0;
  padding: 0;
  width: 100%;
  height: 100%;
  overflow: hidden;
  position: fixed!important;
}

body {
  background: url('water.jpg');
  background-size: 100% 100%;
  background-repeat: no-repeat;
}

.cell {
  z-index:0;
}


@keyframes Fade {
  from {opacity: 1;  transform: translateY(0);}
  to {opacity:0.1;  transform: translateY(-25%);}
}

.scoreNotice {
  width: 100%;
  height: 100%; 
  top: 50%;
  left: 50%;
  margin-top: -50px;
  margin-left: -60px;
  position: relative;
  font-size: 7em;
  color: red;
  animation: Fade 1s ease-out;
  transform: translate3d(0,0,0);
}

.destroy {
  background-color: blue;
}

